import * as React from "react";
import VaskekortEditor from "./VaskekortEditor";
import VaskekortSummary from "./VaskekortSummary";

export enum PaymentType {
  Cash = "Kontant",
  MobileyPay = "MobilePay",
}

export enum PurchaseType {
  Wash = "Vaskekort",
  Key = "Ngle",
  WaitingList = "Venteliste",
  Other = "Andet"
}

export interface IPayment {
  amount?: number;
  payment: PaymentType;
  purchase: PurchaseType;
  note?: string;
  ref: React.RefObject<HTMLInputElement>;
}

interface IState {
  payments: IPayment[];
}

const paymentFactory: () => IPayment = () => ({
  payment: PaymentType.MobileyPay,
  purchase: PurchaseType.Wash,
  ref: React.createRef(),
});

const defaultPayments: IPayment[] = Array.from({ length: 10 }, paymentFactory );

class VaskekortContainer extends React.Component<{}, IState>  {
  constructor(props: {}) {
    super(props);
    this.state = {
      payments: defaultPayments
    }
  }

  public render() {
    return (
      <>
        <h2>Vaskekort</h2>
        { this.state.payments.map((payment, index) => 
          <VaskekortEditor 
            key={index} 
            index={index} 
            payment={payment}
            inputRef={payment.ref}
            onChange={this.handleChange} 
            onSubmit={this.handleSubmit}
          />)}
        <VaskekortSummary payments={this.state.payments} />
        <button onClick={this.clear} type="button">Ryd</button>
      </>
    );
  }

  private handleChange = (index: number, payment: IPayment) => {
    const payments = [...this.state.payments];
    payments[index] = payment;

    if (index === this.state.payments.length - 1) {
      payments.push(paymentFactory());
    }

    this.setState({ payments });
  }

  private handleSubmit = (index: number) => {
    if (index < 0 || index >= this.state.payments.length) {
      return;
    }
    const current = this.state.payments[index].ref.current;
    if (current != null) {
      current.focus();
    }
  }

  private clear = () => {
    this.setState({
      payments: defaultPayments
    })
  }

}

export default VaskekortContainer;



